*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

body {
  letter-spacing: .03em;
  background-color: var(--background-color-body);
  font-family: Golos Text, sans-serif;
  font-size: 14px;
}

.articles, body.modal-open, main {
  overflow: hidden;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.site-nav, .language-list, .hero-section__content-list, .user-manual__list, .our-team__list, .our-team__specialties, .advantages, .articles__cards-wrap, .list-filter, .sort-section, .psychologists-section__filter-list, .sort-list, .sign-for-session__session-type-items, .sign-for-session__session-date-status-list, .sign-for-session__time-options, .session-info__list, .payment-card-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 390px) {
  .container {
    width: 390px;
  }
}

@media screen and (min-width: 744px) {
  .container {
    width: 744px;
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 80px;
    padding-right: 80px;
  }
}

:root {
  --accent-color: #795bda;
  --text-color-nav: #222b40;
  --text-color: #222b40;
  --text-color-white: #fff;
  --text-color-card: #fff;
  --background-color-body: #f3f3f3;
  --background-color: #f7f7f7;
  --time: 250ms;
  --function-animate: cubic-bezier(.4, 0, .2, 1);
}

.page-header {
  border-bottom: 1px solid rgba(34, 43, 64, .1);
  padding: 16px 0;
}

.page-header__conteiner {
  align-items: center;
  display: flex;
  position: relative;
}

.logo__header-icon {
  display: block;
}

.main-nav {
  display: flex;
}

.page-header__logo--padding-top {
  padding-top: 6px;
}

.site-nav {
  display: none;
}

@media screen and (min-width: 1280px) {
  .site-nav {
    flex-wrap: wrap;
    align-items: center;
    margin-left: 217px;
    display: flex;
  }

  .site-nav__item:not(:last-child) {
    margin-right: 30px;
  }

  .site-nav__link {
    letter-spacing: -.05em;
    color: var(--text-color-nav);
    font-family: Golos Text, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    text-decoration: none;
    display: block;
  }

  .site-nav__link:after {
    content: "";
    height: .6px;
    width: 0%;
