}

.page-header__button.login {
  color: #795bda;
  width: 84px;
  height: 32px;
  background-color: rgba(121, 91, 218, .1);
}

.page-header__button.login .icon-login {
  margin-right: 8px;
}

.page-header__button.login:hover {
  background-color: rgba(121, 91, 218, .2);
}

.page-header__button.choose-person {
  color: #fff;
