  }
}

.page-header__button.choose-person:hover {
  background-color: #694fbf;
}

.menu-toggle {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0 18px 0 0;
  padding: 0;
  display: flex;
}

