}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu-toggle {
  color: #fff;
  position: absolute;
  top: 16px;
  right: 16px;
}

.mobile-menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mobile-menu .link {
  color: #fff;
  padding: 10px;
  text-decoration: none;
  display: block;
}

.language-menu {
  margin-left: auto;
  margin-right: 16px;
  position: relative;
}

.language-toggle {
  cursor: pointer;
  align-items: center;
  display: flex;
}

.language {
  letter-spacing: -.05em;
  color: var(--text-color-nav);
