}

.grey_bg {
  max-width: 225px;
  background: #edebf2;
  border-radius: 16px;
  margin: 0 auto;
  padding: 10px 20px;
  position: relative;
  transform: translateY(-18px);
}

.grey_bg:before {
  content: "";
  width: 10px;
  height: 10px;
  background: #edebf2;
  border-radius: 50%;
  position: absolute;
  top: -6px;
  right: -6px;
}

.grey_bg:after {
  content: "";
  width: 5px;
  height: 5px;
  background: #edebf2;
  border-radius: 50%;
  position: absolute;
  top: -12px;
  right: -8px;
}

.grey_bg span {
  letter-spacing: -.01em;
  color: #222b40;
  max-width: 185px;
  font-size: 16px;
  font-weight: 500;
  line-height: 150%;
