}

.item__wrap:before, .item__wrap:after {
  content: "";
  width: 19px;
  height: 2px;
  background: #222b40;
  transition: all .3s;
  position: absolute;
  top: 50%;
}

.item__wrap:before {
  right: 0;
  transform: rotate(90deg);
}

.item__wrap:after {
  right: 0;
  transform: rotate(0deg);
}

.block__title.active:before {
  background: none;
}

.block__title.active:after {
